
import api from '../../../api';
import XHeader from 'vux/src/components/x-header/index.vue'
;
import vSingleCard from '../../../components/card/detail/singleCard.vue';
export default {
  components: {
    XHeader,
    vSingleCard
  },
  // 解除keep-alive的缓存
  beforeRouteEnter: (to, from, next) => {
    next(vm => {
      window.onscroll = () => {
        let opa = window.pageYOffset / 222;
        if (opa > 1) {
            vm.tName = vm.datas.name;
            vm.opacity = 1;
        } else {
            vm.tName = '歌单';
            vm.opacity = 0;
        }
      };
    });
  },
  // 路由离开时清除onscroll事件
  beforeRouteLeave: (to, from, next) => {
    window.onscroll = null;
    next();
  },
  data () {
    return {
      datas: {},
      tName: '歌单',
      creator: {},
      data: [],
      index: '',
      list: [],
      backgroundColor: '',
      opacity: 0
    };
  },
  mounted: function() {
    this.getPlayListDetail();
  },
  methods: {
    jumpUserDetail(id) {
      this.$router.push({
        path: '/user/' + id
      });
    },
    getPlayListDetail () {
      this.$store.commit('update_loading', true);
      api.getPlaylistDetailResource(this.$route.params.id).then((response) => {
        this.datas = response.data.playlist;
        this.list = response.data.playlist.tracks;
        this.creator = response.data.playlist.creator;
        // $nextTick() 在dom 重新渲染完后执行
        this.$nextTick(() => {
          this.$store.commit('update_loading', false);
        });
      }).catch((error) => {
        console.log('加载歌单信息出错:' + error);
      });
    }
  },
  computed: {
    playListImage() {
      return '' || (this.datas.picUrl);
    },
    creatorImage() {
      return '' || this.creator.avatarUrl;
    }
  },
  filters: {

  }
};



